import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { FormGroup } from '@angular/forms';

@Injectable({
  providedIn: 'root'
})
export class UsercreationService {

  constructor(private http:HttpClient) { }

  createUser(formdata){
    return this.http.post('http://127.0.0.1:8000/createuser',formdata)
  }

  addBankDetails(bankdetails:FormGroup){
    return this.http.post('http://127.0.0.1:8000/bankdetails',bankdetails)
  }
}
