import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { ConfirmedValidator1 } from 'src/app/shared/validator';
import { FileUploader, FileLikeObject } from 'ng2-file-upload';
import { HttpClient } from '@angular/common/http';
import { UsercreationService } from '../usercreation.service';
import { NotificationService } from 'src/app/registration/notification.service';
import { ConfirmedValidator } from '../userdetails/userdetails.validator';

@Component({
  selector: 'app-mo-bankdetails',
  templateUrl: './mo-bankdetails.component.html',
  styleUrls: ['./mo-bankdetails.component.css']
})
export class MoBankdetailsComponent implements OnInit {

  status:boolean=false;
  constructor(private fb1:FormBuilder,private http:HttpClient,private userservice:UsercreationService,private notification:NotificationService) { }

  public uploader: FileUploader = new FileUploader({});
  public hasBaseDropZoneOver: boolean = false;
  currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
  bankdetails=this.fb1.group({
    'accountname':new FormControl(null,Validators.required),
    'bankname':new FormControl(null,Validators.required),
    'accountnumber':new FormControl(null,Validators.required),
    'confirmaccnumber':new FormControl(null,Validators.required),
    'branchname':new FormControl(null,Validators.required),
    'ifsccode':new FormControl(null,Validators.required),
    'validfrom':new FormControl(null,Validators.required),
    'validtill':new FormControl(null,Validators.required)
  },
  { 
    validator: ConfirmedValidator('accountnumber', 'confirmaccnumber')
  }
  )


  get f(){
    return this.bankdetails.controls;
  }
  
  fileOverBase(event):  void {
    this.hasBaseDropZoneOver  =  event;
  }
  getFiles(): FileLikeObject[] {
    return this.uploader.queue.map((fileItem) => {
      return fileItem.file;
    });
  }
  ngOnInit(): void {
  }

  onAddDetails(){
  
    this.userservice.addBankDetails(this.bankdetails.value).subscribe(res=>{
      console.log(res);
      this.notification.success("Added Successfully");
      this.bankdetails.reset();

    },error=>{
      this.notification.warn("Failed to add details");
      console.log("error occured",error);
    });

  }
  upload(bankcheque){
    let files = this.getFiles();
    // let requests = [];
    // // files.forEach((file) => {
    let extension=files[0].name
    console.log(files);
    let formData = new FormData();
    formData.append('file' , files[0].rawFile,this.currentUser.userName+'@'+bankcheque+extension);

    this.http.post('http://127.0.0.1:8000/imageData',formData).subscribe(
      res=>{
        this.status=true;
        this.uploader.clearQueue();
      },
      err => {  
        console.log(err);
      }
    );   


  }
}
