import { Component, OnInit, ViewChild, ElementRef, OnDestroy } from '@angular/core';
import { PostsService } from '../registration/registration.service';
import { FormGroup ,FormBuilder} from '@angular/forms';
import { AuthService } from '../auth/auth.service';
import { Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit,OnDestroy {

  @ViewChild('fileUpload') fileUploadEl: ElementRef;
  contents:any[];
  file:File;
  isAuthenticated=true;
  shortName:string=null;
  selected:string=null;
  userName:string;
  isFinance:boolean=true;
  private userSub:Subscription;
  constructor(private posts:PostsService, private fb:FormBuilder,public authService:AuthService,
              private route:Router,private http:HttpClient) { }

  ngOnInit(): void {
    
  
    this.userSub=this.authService.user.subscribe(user=>{
      this.isAuthenticated= !!user;
    });
    var currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
    console.log("currenr user",currentUser);
    // var token = currentUser.token; // your token
    this.isAuthenticated=currentUser.isSuperUser;
    if(currentUser.usertype =='Finance'){
      this.isFinance=false;
    }
    else{
      this.isFinance=true;
    }
    
    this.shortName=currentUser.shortName
    this.userName=currentUser.userName
  
  }
  getbankdetails(){
    this.http.get('http://127.0.0.1:8000/srldc-bankdetails').subscribe(res=>{

    console.log(res);
      var fileText=res;
      var fileName = "SRLDC_MO BankDetails.txt"
      this.saveTextAsFile(fileText, fileName);
    
    },error=>{
      console.log("errors occured",error);
    })
  }
  allAccountDetails(){

    this.http.get('http://127.0.0.1:8000/all-mobankdetails').subscribe(res=>{

    console.log(res);
      var fileText=res;
      var fileName = "SRLDC_MO_ALL_BankDetails.txt"
      this.saveTextAsFile(fileText, fileName);
    
    },error=>{
      console.log("errors occured",error);
    })
  }
  saveTextAsFile(data,filename){

    if(!data) {
        console.error('Console.save: No data')
        return;
    }

    if(!filename) filename = 'console.json'

    var blob = new Blob([data], {type: 'text/plain'}),
        e    = document.createEvent('MouseEvents'),
        a    = document.createElement('a')
// FOR IE:

    if (window.navigator && window.navigator.msSaveOrOpenBlob)
      {
        window.navigator.msSaveOrOpenBlob(blob, filename);
      }
    else
      {
        var e = document.createEvent('MouseEvents'),
            a = document.createElement('a');

        a.download = filename;
        a.href = window.URL.createObjectURL(blob);
        a.dataset.downloadurl = ['text/plain', a.download, a.href].join(':');
        e.initEvent('click', true, false);
        
        a.dispatchEvent(e);
      }
    }
 
  loggingoff(){
    
    this.posts.signupForm.reset();
    var currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
    sessionStorage.removeItem(currentUser);
    sessionStorage.clear();
    this.route.navigate(['/auth']);
  }

ngOnDestroy(){

}
}



  

