import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-home-notification',
  templateUrl: './home-notification.component.html',
  styleUrls: ['./home-notification.component.css']
})
export class HomeNotificationComponent implements OnInit {

  formdata:Boolean=false;
  approved:Boolean=false;
  rejected:Boolean=false;generated:Boolean=false;
  pending:Boolean=false;
  gen_billYear:String;gen_billStartDate:Date;gen_billEndDate:Date;gen_billRevision:string;
  bank_pending:Boolean=false;
  rejectedReason:string;
  pendingUsers=[];
  financeApproved=[];
  financeRejected=[];
  pendingContacts=[];
  pendingBank=[];
  approvedDate:Date;
  conApprovedDate:Date;
  bankApprovedDate:Date;
  applicationNumber:string;
  applicationNumber1:string;
  applicationNumber2:string;
  contactUpdation:string;
  
  isSuperUser:boolean;
  isFinance:boolean=false;
  isUpdatedContact:boolean=false;
  isUpdatedBank:boolean=false;
  

  constructor(private http:HttpClient) { }
  ngOnInit(): void {

    var currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
  
    this.isSuperUser=currentUser.isSuperUser;
    if(currentUser.usertype =='Finance'){
      this.isFinance=true;
    }

    this.http.post("http://127.0.0.1:8000/pendingUsers",currentUser.registration_id).subscribe(res=>{
          
        
          for(const id in res){
            if(id=='admin_data')
            {
              this.pendingUsers.push(res[id]);
            }
            else if(id=='finance_approved'){
              this.financeApproved.push(res[id]);
            }
            else if(id =='finance_rejected'){
              this.financeRejected.push(res[id])
            }
          }
          if(res['updated_contact']){
            this.isUpdatedContact=true
            this.conApprovedDate=res['updated_contact']['approvedDate'];
          }

          if(res['updated_bank']){
            this.isUpdatedBank=true
            this.bankApprovedDate=res['updated_bank']['validfrom'];
          }
        
          for(const name in res){
            if(name=='pending_contacts')
            {
              this.pendingContacts.push(res[name]);
            }
          }

          for(const name in res){
            if(name=='pending_bank')
            {
              this.pendingBank.push(res[name]);
            }
          }
         
          for(const key in res){
            
            if(key=='formdata'){
              if(res[key].length >0 ){
                this.applicationNumber=res[key][1];
                this.formdata=true;
                }
                else{
                  this.formdata=false;
                }
            }
            else if(key=='approved'){
              if(res[key].length >0 ){
                this.applicationNumber1=res[key][1]
                this.approvedDate=res[key][2];
                this.approved=true;
              }
              else{
                this.approved=false;
              }
            }
            else if(key=='rejected'){
              
              if(res[key].length >0){
                
                this.applicationNumber2=res[key][1]
                this.rejected=true;
                this.rejectedReason=res[key][0];
              }
              else{
                this.rejected=false;
              }
            }
            else if(key=='bills_generated'){
              
              if(res[key].length >0){
                
                this.gen_billYear=res[key][0]
                this.gen_billStartDate=res[key][1]
                this.gen_billEndDate=res[key][2]
                this.gen_billRevision=res[key][3]
                this.generated=true;
                this.rejectedReason=res[key][0];
              }
              else{
                this.rejected=false;
              }
            }
            else if(key=='pending'){
              
              if(res['pending']){
                this.pending=true;
                // this.rejectedReason=res['rejected'][0];
              }
              else{
                this.pending=false;
              }
            }
            else if(key=='bank_pending'){
              
              if(res['bank_pending']){
                this.bank_pending=true;
                // this.rejectedReason=res['rejected'][0];
              }
              else{
                this.bank_pending=false;
              }
            }

          }  
    });
//Finance Verification Status
    
  }

 

}
