import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { PaymentDetailsService } from './payment-details.service';
import { FormBuilder, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { BillData } from '../shared/generated_bill.model';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { SelectionModel } from '@angular/cdk/collections';
import { ModalService } from '../_modal';
import { HttpClient } from '@angular/common/http';
import { NotificationService } from '../registration/notification.service';
import { error } from 'protractor';
import { toInteger, isInteger } from '@ng-bootstrap/ng-bootstrap/util/util';
import { DateListService } from '../configure/date-list.service';
import { MatGridTileHeaderCssMatStyler } from '@angular/material/grid-list';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';



@Component({
  selector: 'app-payment-details',
  templateUrl: './payment-details.component.html',
  styleUrls: ['./payment-details.component.css']
})
export class PaymentDetailsComponent implements OnInit {

  generatedBills:Array<string>=[];
  years=['2017-18','2018-19','2019-20','2020-21']
  weeknos=[1,2,3,4,5,6,7,8,9]
  status=["Notified","Partial","Complete"]
  paytype=["RTGS","DD","NEFT","Others"]
  payorrecieve=["Payable","Receivable"]
  startDate:Date;
  endDate:Date
  paidamount:Number;
  splitvalues:any=[];
  showsplitcomp:boolean=false;
  elementData:any;
  currentUser = JSON.parse(sessionStorage.getItem('currentUser'));

  paymentButton:Boolean=true;
  constructor(private weekBills:PaymentDetailsService,private fb:FormBuilder,
    private modalService: ModalService,public checked:PaymentDetailsService,
    private notificationService:NotificationService,private date:DateListService,
    public dialog: MatDialog) { }
  paymentForm=this.fb.group({
    'year':['2020-21',Validators.required],
    'weekno':[null,Validators.required],
    'weekStartDate':[null,Validators.required],
    'weekEndDate':[null,Validators.required],
    'status':["Notified",Validators.required],
    'payorreceive':[null,Validators.required]
  })
  listData: MatTableDataSource<BillData>;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  displayedColumns: string[] = ['select','Entity','Letter_refno','Revision_no','DevFinal','Outstandings','PayableorReceivable'];
  showFooter='off';
  selection = new SelectionModel<BillData>(false);
  selectedCheckbox:BillData; 
  paymenttype:string=''; 
  paymentref:string=''; 
  paymentdate:Date; 
  paymentamount:Number; 
  error=null;
  ngOnInit(): void {  
  }

  openDialog() {
    this.checked.populateForm(this.selectedCheckbox);
    const dialogRef = this.dialog.open(DialogExampleComponent,{
      width: '950px',
      data:{check:this.selectedCheckbox}
    });

    dialogRef.afterClosed().subscribe(result => {
      this.onSubmit();
    });
  }

  get init(): any {
    return {
      first: 0
     
    };
  }
  weekChange(event)
  {
    this.startDate=this.date.date_list[0][0];
    this.endDate=this.date.date_list[0][1];
  }
  onSubmit(){
    
    this.weekBills.fetchConfigDetails(this.paymentForm.value).subscribe(response=>{
      
      this.listData = new MatTableDataSource(response[0]['Bills']);
      this.listData.sort=this.sort;
      this.listData.paginator=this.paginator;
      this.showFooter='on'

      
    })
  }

  onSplit(element)
  {
    this.showsplitcomp=true;
    this.splitvalues=[];
    this.checked.splitPayments(element).subscribe(res=>{
      for(const key in res)
      { 
        this.splitvalues.push(res[key]);
      }
      this.splitvalues.push(element);
    },
    error=>{
      console.log("error occured",error);
    })
  }
  
  onSelect(element:BillData){
    
    this.selectedCheckbox=element;
    
  }
  visibility(event:Event){
  
    if(this.currentUser['registration_id']=='SRADMIN')
    {
    this.paymentButton=!event['checked'];
    }
    else if(this.paymentForm.value['payorreceive']=='Receivable')
    {
      this.paymentButton=true;
    }
    else{
      this.paymentButton=false;
    }
  }
  openModal(id: string) {
  this.modalService.open(id);
  }

  
  closeModal(id: string) {
  this.modalService.close(id);
  }

  onChecked(){

    this.checked.populateForm(this.selectedCheckbox);
  }
  
  onSaveChanges(paymentDetails)
  {   
    
    
    var payamount:number=+paymentDetails["payamount"]
    
    var actalamount:number=this.checked.selectedRow.value['DevFinal']
    if(payamount == actalamount)
    {
      var changeStatus="Complete"
    }
    else if( payamount<actalamount)
    {
      var changeStatus="Partial"
    }

    if(payamount>actalamount)
    {
      this.notificationService.warn("Excess amount is not acceptable")
    }
    else{
    
    this.checked.paymentDetails(paymentDetails["paytype"],paymentDetails["payrefno"],paymentDetails["paydate"],paymentDetails["payamount"],paymentDetails["remarks"],changeStatus,this.currentUser['fullname']).subscribe(res=>{
      
      this.notificationService.success('! Submitted successfully');
      this.onSubmit();
    },
    error=>{
      this.error.next(error.message);
  })
    
  }
}
}

@Component({
  selector: 'dialog-content-example-dialog',
  templateUrl: 'dialog.html',
})
export class DialogExampleComponent {

  paymenttype:string=''; 
  paymentref:string=''; 
  paymentdate:String; 
  paymentamount:Number; 
  paytype=["RTGS","DD","NEFT","Others"]
  newdate:string;
  currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
  constructor(
    public dialogRef: MatDialogRef<DialogExampleComponent>,
    @Inject(MAT_DIALOG_DATA) public data,private fb:FormBuilder,public checked:PaymentDetailsService,
    private notificationService:NotificationService) {}
  
    paymentDetails=this.fb.group({
      'paytype':[null,Validators.required],
      'payrefno':[null,Validators.required],
      'paydate':[null,[Validators.required]],
      'payamount':[null,Validators.required],
      'remarks':[null]
    })

    get init(): any {
      return {
        first: 0
       
      };
    }
    onSaveChanges(paymentDetails)
  {   
    
    
    var payamount:number=+paymentDetails["payamount"]
    
    var actalamount:number=this.checked.selectedRow.value['DevFinal']
    if(payamount == actalamount)
    {
      var changeStatus="Complete"
    }
    else if( payamount<actalamount)
    {
      var changeStatus="Partial"
    }

    if(payamount>actalamount)
    {
      this.notificationService.warn("Excess amount is not acceptable")
    }
    else{
    
      
      var d = new Date(paymentDetails['paydate']),
  month = '' + (d.getMonth() + 1),
  day = '' + d.getDate(),
  year = d.getFullYear();

if (month.length < 2) month = '0' + month;
if (day.length < 2) day = '0' + day;

this.newdate= [year, month, day].join('-');

    this.checked.paymentDetails(paymentDetails["paytype"],paymentDetails["payrefno"],this.newdate,paymentDetails["payamount"],paymentDetails["remarks"],changeStatus,this.currentUser['fullname']).subscribe(res=>{
      
      this.notificationService.success('! Submitted successfully');
      this.dialogRef.close("closed successfully");
      
    },
    error=>{
      this.notificationService.warn('!Failed to Submit the Details');
      this.dialogRef.close("failed to close");
     })
    }
  }
    
}