import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject } from 'rxjs';
import { catchError, mapTo, tap } from 'rxjs/operators';
import { FormBuilder, Validators } from '@angular/forms';
import { User } from './user.model';
import { StringDecoder } from 'string_decoder';
import { Router } from '@angular/router';

export interface AuthResponseData {
  token: string;
  user_id: string;
  email: string;
  isSuperUser: boolean;
  shortName:string;
  userName:string;
  fullname:string;
  usercategory:string;
  usertype:string;
}

@Injectable({
  providedIn: 'root'
})

export class AuthService {
  
  public user=new Subject<User>();
  constructor(private http: HttpClient,private fb:FormBuilder,private router:Router) {}
  
  login=this.fb.group({
    'username':[null,Validators.required],
    'password':[null,Validators.required]
  })

  signup=this.fb.group({
    'username':[null,Validators.required],
    'password':[null,Validators.required],
    'entityName':[null,Validators.required]
  })

  loginForm():Observable<any>{
    return this.http.post<AuthResponseData>('http://127.0.0.1:8000/api-token-login/',this.login.value).pipe(tap(resData => {
      console.log("after logs in ",resData);
      sessionStorage.setItem('currentUser', JSON.stringify({ token:resData.token, user_id: resData.user_id,
                                                            isSuperUser:resData.isSuperUser,
                                                            shortName:resData.shortName,
                                                            userName:resData.userName ,
                                                            registration_id:resData.registration_id,
                                                            fullname:resData.fullname,
                                                            usercategory:resData.usercategory,
                                                            usertype:resData.usertype}));
      
    }));

  }

  checkUser(){
    var currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
    if(currentUser){
      // this.http.post('http://127.0.0.1:8000/auth-token-verify/',currentUser['token']).subscribe(res=>{
      //   console.log("authenticating user",res);
      //   return true;
      // })
      return true;
    }
    else{
      return this.router.navigate(['auth']);
    }
  }

}