import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ModalService } from 'src/app/_modal';
import { BigModalService } from 'src/app/_bigmodal/bigmodal.service';
import { NotificationService } from 'src/app/registration/notification.service';

@Component({
  selector: 'app-pending-bank',
  templateUrl: './pending-bank.component.html',
  styleUrls: ['./pending-bank.component.css']
})
export class PendingBankComponent implements OnInit {
  pendingBank=[];
  selected:any;
  remarks:string=null;
  anypendingDetails:boolean=true;
  imageURLList:Array<string>[]=[];
  emptyList:boolean=true;
  constructor(private http:HttpClient,private modalService: BigModalService,private notification:NotificationService) { }

  ngOnInit(): void {
    // this.http.get('http://127.0.0.1:8000/editedpendingBankDetails').subscribe(response=>{
      
    //   for(const key in response){
    //       this.pendingBank.push(response[key]);
    //   }
    // },error=>{
    //   console.log("error occured",error);
    // })
    this.onFetch();
  }

  onView(bank){
    this.selected=bank;
    
  }
  onApprove(){
    
    this.http.post('http://127.0.0.1:8000/final-bank-approval',{id:this.selected.id,register_id:this.selected.register_id_id}).subscribe(response=>{
      this.onFetch();
    this.modalService.close('approve-modal');
    this.notification.success("Approved Successfully")
    },error=>{
      console.log(error);
    });
  }

  onReject(){

  this.http.post('http://127.0.0.1:8000/final-bank-rejection',{id:this.selected.id,remarks:this.remarks}).subscribe(response=>{
      this.onFetch();
      this.modalService.close('reject-modal');
      this.notification.success("Rejected Successfully")
    },error=>{
      this.notification.warn("Error Occured!");
    });

  }
  onFetch(){
    return this.http.get('http://127.0.0.1:8000/editedpendingBankDetails').subscribe(response=>{
          
          this.pendingBank=[];
          for(const key in response){
              
              this.pendingBank.push(response[key]);
          }
          },error=>{  
          this.notification.warn("Failed to Fetch Bank Details");
        });
  }
  openModal1(id: string) {
    this.modalService.open(id);
  }

  
  openModal(id:string){
    
    this.http.post('http://127.0.0.1:8000/getBankImage',this.selected).subscribe(res=>{

    console.log(res);
        this.imageURLList=[];
          for(const key in res)
          {
            this.imageURLList.push(res[key]);

          }
          if(this.imageURLList.length > 1)
          {
            this.emptyList=false;
          }
        });

       
        
    this.modalService.open(id);
    
  }

  closeModal1(id: string) {
    this.modalService.close(id);
  }

}
