import { FormGroup } from '@angular/forms';

export function ConfirmedValidator(controlName: string, matchingControlName: string,
                                controlName2: string, matchingControlName2: string ,
                                controlName3: string, matchingControlName3: string)
                                {
    return (formGroup: FormGroup) => {
        const control = formGroup.controls[controlName];
        const matchingControl = formGroup.controls[matchingControlName];
        const control2 = formGroup.controls[controlName2];
        const matchingControl2 = formGroup.controls[matchingControlName2];
        const control3 = formGroup.controls[controlName3];
        const matchingControl3 = formGroup.controls[matchingControlName3];

        if ((matchingControl.errors && !matchingControl.errors.confirmedValidator)&&
        (matchingControl2.errors && !matchingControl2.errors.confirmedValidator)&&
        (matchingControl3.errors && !matchingControl3.errors.confirmedValidator)) {
            
            return;
        }
        if ((control.value !== matchingControl.value)&&
        (control2.value !== matchingControl2.value) &&
        (control3.value !== matchingControl3.value)) {
            matchingControl.setErrors({ confirmedValidator: true });
        } else {
            matchingControl.setErrors(null);
        }
    }
}

export function ConfirmedValidator1(controlName: string, matchingControlName: string){
    return (formGroup: FormGroup) => {
        const control = formGroup.controls[controlName];
        const matchingControl = formGroup.controls[matchingControlName];
        if (matchingControl.errors && !matchingControl.errors.confirmedValidator) {
            return;
        }
        if (control.value !== matchingControl.value) {
            matchingControl.setErrors({ confirmedValidator: true });
        } else {
            matchingControl.setErrors(null);
        }
    }
}