import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { FormBuilder } from '@angular/forms';
import { NotificationService } from '../registration/notification.service';
import { FileLikeObject, FileUploader } from 'ng2-file-upload';
enum CheckBoxType { DSM,RRAS,AGC,REACTIVE,SCED,CONGESTION,NONE };
@Component({
  selector: 'app-edit-bankdetails',
  templateUrl: './edit-bankdetails.component.html',
  styleUrls: ['./edit-bankdetails.component.css']
})
export class EditBankdetailsComponent implements OnInit {

  public uploader: FileUploader = new FileUploader({});
  public hasBaseDropZoneOver: boolean = false;
  status:boolean=false;
  constructor(private http:HttpClient,private fb:FormBuilder,private notification:NotificationService) { }

  currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
    
  accountName:string;
  accountNumber:Number;
  bankName:string;
  branchName:string;
  ifscCode:string;
  choosenAccount:string;
  check_box_type = CheckBoxType;

  currentlyChecked: CheckBoxType;
  ngOnInit(): void {
  }
 

  onSelectDSM(event,targetType: CheckBoxType){
    if(event.checked){
      this.SelectedAccount('DSM').subscribe(res=>{
       
        for(const key in res){
          this.accountName=res[key]['accountName'];
          this.accountNumber=res[key]['accountNumber'];
          this.bankName=res[key]['bankName'];
          this.branchName=res[key]['branchName'];
          this.ifscCode=res[key]['ifscCode'];
          this.choosenAccount='DSM';
          
        }
      });
    }
    this.checkedOne(targetType);
  }
  onSelectRRAS(event,targetType: CheckBoxType){
    if(event.checked){
      this.SelectedAccount('RRAS').subscribe(res=>{
       
        for(const key in res){
          this.accountName=res[key]['accountName4'];
          this.accountNumber=res[key]['accountNumber4'];
          this.bankName=res[key]['bankName4'];
          this.branchName=res[key]['branchName4'];
          this.ifscCode=res[key]['ifscCode4'];
          this.choosenAccount='RRAS';
          
        }
      });
    }
    this.checkedOne(targetType);
  }
  onSelectAGC(event,targetType: CheckBoxType){
    if(event.checked){
     
      this.SelectedAccount('AGC').subscribe(res=>{
        for(const key in res){
          this.accountName=res[key]['accountName5'];
          this.accountNumber=res[key]['accountNumber5'];
          this.bankName=res[key]['bankName5'];
          this.branchName=res[key]['branchName5'];
          this.ifscCode=res[key]['ifscCode5'];
          this.choosenAccount='AGC';
          
        }
      });
    }
  this.checkedOne(targetType);

  }
  onSelectREACTIVE(event,targetType: CheckBoxType){
    if(event.checked){
      this.SelectedAccount('REACTIVE').subscribe(res=>{
        for(const key in res){
          this.accountName=res[key]['accountName2'];
          this.accountNumber=res[key]['accountNumber2'];
          this.bankName=res[key]['bankName2'];
          this.branchName=res[key]['branchName2'];
          this.ifscCode=res[key]['ifscCode2'];
          this.choosenAccount='REACTIVE';
          
        }
      });
    }
    this.checkedOne(targetType);
  }
  onSelectSCED(event,targetType: CheckBoxType){
    if(event.checked){
      this.SelectedAccount('SCED').subscribe(res=>{
        for(const key in res){
          this.accountName=res[key]['accountName6'];
          this.accountNumber=res[key]['accountNumber6'];
          this.bankName=res[key]['bankName6'];
          this.branchName=res[key]['branchName6'];
          this.ifscCode=res[key]['ifscCode6'];
          this.choosenAccount='SCED';
          
        }
      });
    }
    this.checkedOne(targetType);
  }
  onSelectCONGESTION(event,targetType: CheckBoxType){
    if(event.checked){
      this.SelectedAccount('CONGESTION').subscribe(res=>{
        for(const key in res){
          this.accountName=res[key]['accountName3'];
          this.accountNumber=res[key]['accountNumber3'];
          this.bankName=res[key]['bankName3'];
          this.branchName=res[key]['branchName3'];
          this.ifscCode=res[key]['ifscCode3'];
          this.choosenAccount='CONGESTION';
          
        }
      });
    }
    this.checkedOne(targetType);
  }

  SelectedAccount(acc:string){
   return this.http.post('http://127.0.0.1:8000/getaccountdetails',{id:this.currentUser['registration_id'],type:acc})
  }

  checkedOne(targetType){
    if(this.currentlyChecked === targetType) {
      this.currentlyChecked = CheckBoxType.NONE;
      return;
    }

    this.currentlyChecked = targetType;

  }
  onEdit(){
    if(this.choosenAccount){


      this.http.post('http://127.0.0.1:8000/updatebankdetails',{type:this.choosenAccount,accName:this.accountName,accNum:this.accountNumber,bankName:this.bankName,branchName:this.branchName,ifsc:this.ifscCode,id:this.currentUser['registration_id'],fullname:this.currentUser['fullname']}).subscribe(res=>{
        this.accountName="";this.bankName="";this.branchName="";this.ifscCode="";
        this.notification.success('Success |Pending for approval');
      },error=>{
        this.notification.warn('Failed to Update');
      })
    }
  else{
    this.notification.warn("Please choose atleast one account")
  }
    
  }
  fileOverBase(event):  void {
    this.hasBaseDropZoneOver=event;
  }
  getFiles(): FileLikeObject[] {
    return this.uploader.queue.map((fileItem) => {
      return fileItem.file;
    });
  }

  upload(){
    let files = this.getFiles();
    if(files[0])
    {
      let requests = [];
    let extension=files[0].name
    
    let formData = new FormData();
    formData.append('file' , files[0].rawFile, this.currentUser.registration_id+'@'+this.choosenAccount+extension);    
    
    this.http.post('http://127.0.0.1:8000/updateImage',formData).subscribe(res=>{
      this.status=true;
    },error=>{
      this.notification.warn("Not Uploaded");
    }) 
    }
    else{
      this.notification.warn("Please Choose File")
    }
     
  }
  
}
