import { ModifyBillsComponent } from './configure/modify-bills/modify-bills.component';
import { UserdetailsComponent } from './usercreation/userdetails/userdetails.component';


const routes: Routes = [
 
  {
    path: "",
    loadChildren: () =>
      import("./auth/auth-routing.module").then(m => m.AuthRouting)
  },
  {
    path: "home",
    loadChildren: () =>
      import("./header/header-routing.module").then(m => m.HeaderRouting)
  },
  {
    path: "new-chat",
    loadChildren: () =>
      import("./new-chat/new-chat-routing.module").then(m => m.NewChatRouting)
  },
  {
    path: "user",
    loadChildren: () =>
      import("./usercreation/usercreation-routing.module").then(m => m.UserCreation)
  },

  {
    path: "admin",
    loadChildren: () =>
      import("./super-user/supuser-routing.module").then(m => m.SuperUserRouting)
  },
  {
    path: "registration",
    loadChildren: () =>
      import("./registration/registration-routing.module").then(m => m.RegistrationRouting)
  },
  {
    path: "config",
    loadChildren: () =>
      import("./configure/config-routing.module").then(m => m.ConfigRouting)
  },
  {
    path: "payment",
    loadChildren: () =>
      import("./payment-details/payment-routing.module").then(m => m.PaymentRouting)
  },

  {path:'fileUpload',component: FileUploadComponent},
  {path:'**',redirectTo:'home',pathMatch:'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {

  constructor(){}

  
 }
