import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { NotificationService } from '../registration/notification.service';

@Component({
  selector: 'app-edit-contact',
  templateUrl: './edit-contact.component.html',
  styleUrls: ['./edit-contact.component.css']
})
export class EditContactComponent implements OnInit {

  constructor(private fb:FormBuilder,private http:HttpClient,private notificationService:NotificationService) { }

  currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
  
  ngOnInit(): void {
    
    this.http.post("http://127.0.0.1:8000/edit-details",this.currentUser.registration_id).subscribe(response=>{
        console.log("edit details",response);
      this.editContact.patchValue(response);
    },error=>{
      console.log("error occured",error);
    })
  }
  editContact=this.fb.group({
    'contactName':[null,[Validators.required,Validators.pattern('^[a-zA-Z ]*$')]],
    'designation':[null,Validators.required],
    'telephone':[null,[Validators.required,Validators.pattern("^[0-9]*$")]],
    'contactName2':[null,[Validators.required,Validators.pattern('^[a-zA-Z ]*$')]],
    'designation2':[null,Validators.required],
    'telephone2':[null,[Validators.required,Validators.pattern("^[0-9]*$")]],
    'contactName3':[null,Validators.pattern('^[a-zA-Z ]*$')],
    'designation3':[null],
    'telephone3':[null,Validators.pattern("^[0-9]*$")]
    
  })

  onEdit(){
    console.log(this.editContact.value);
    this.http.post("http://127.0.0.1:8000/edited-details",{formcontent:this.editContact.value,register_id:this.currentUser.registration_id,fullname:this.currentUser.fullname}).subscribe(response=>{

        this.editContact.reset();
        this.notificationService.success("Successfully Submitted | pending for approval");
    },error=>{
      this.notificationService.warn("Failed to Submit Details");
      console.log(error);
    })
  }
}
