import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { DateListService } from '../date-list.service';
import { HttpClient } from '@angular/common/http';
import { NotificationService } from 'src/app/registration/notification.service';
import { ModalService } from 'src/app/_modal';

@Component({
  selector: 'app-modify-bills',
  templateUrl: './modify-bills.component.html',
  styleUrls: ['./modify-bills.component.css']
})
export class ModifyBillsComponent implements OnInit {

  years=['2016-17','2017-18','2018-19','2019-20','2020-21']
  weeknos=[1,2,3,4,5,6,7,9,12]
  status=["Notified","Partial","Completely Paid"]
  payorreceive=["Payable","Receivable"]
  revisions=["Revision 0","Revision 1","Revision 2","Revision 3"]
  startDate:Date;
  endDate:Date;
  allbills=[];
  currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
  constructor(private fb:FormBuilder,private date:DateListService,private http:HttpClient,private notification:NotificationService,private modalService:ModalService) { }

  ngOnInit(): void {
  }

  viewbills=this.fb.group({
    'year':[null,Validators.required],
    'weekno':[null,Validators.required],
    'weekStartDate':[null,Validators.required],
    'weekEndDate':[null,Validators.required],
    'status':["Notified",Validators.required],
    'payorreceive':["Payable",Validators.required]
  })
  popupbill:FormGroup=new FormGroup({
    Letter_refno:new FormControl(null),
    Revision_no:new FormControl(null),
    Entity:new FormControl(null),
    DevFinal:new FormControl(null),
    PayableorReceivable:new FormControl(null),
    id:new FormControl(null),
    remarks:new FormControl(null)
  })
  weekChange(event)
  {
    this.startDate=this.date.date_list[0][0];
    this.endDate=this.date.date_list[0][1];
  }
  onSubmit()
  { 
    this.allbills=[];
    if(this.viewbills.valid)
    {
      this.http.post('http://127.0.0.1:8000/file/view-bills',{Bills:this.viewbills.value,id:this.currentUser['registration_id']}).subscribe(res=>{
        for(const key in res)
        {
          
          this.allbills.push(res[key]);
        }
    });
    }
    else
    {
      this.notification.warn("Please Select Year and Week No");
    }
    
  }

  

  onUpdate()
  {
    console.log("upated values",this.popupbill.value);
    if(this.popupbill.value['remarks'])
    {
      this.http.post('http://127.0.0.1:8000/file/update-bills',this.popupbill.value).subscribe(res=>{
        this.onSubmit();
        this.closeModal('edit-modal');
        this.notification.success("Updated Successfully");

      });
    }
    else{
      this.notification.warn("Please enter Remarks")
    }
  }
  openModal(id,bill)
  { 
    this.popupbill.patchValue(bill);
    this.modalService.open(id);
  }

  closeModal(id)
  {
    this.modalService.close(id);
  }
}
