import { Injectable } from '@angular/core';
import { ChatMessage } from '../shared/chat-message.model';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class ChatServiceService {

  chatMessage: ChatMessage;
  chatMessages:any;
  feed:Array<string>[]=[];
  currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
  constructor(private http:HttpClient,private router:Router) {
    if(!this.currentUser)
    {
      this.router.navigate(['/auth']);
    }
   }

  sendMessage(msg: string) {

   
    this.http.post('http://127.0.0.1:8000/file/chatMessages',{message: msg,
      sender:this.currentUser['userName'],short_name: this.currentUser.shortName}).subscribe(res=>{
        console.log("success",res);
      })
    
  }
  getUsers()
  {
    return this.http.get('http://127.0.0.1:8000/file/chatUsers')
  }

  getMessages(sender:string,receiver:string){
    return this.http.post('http://127.0.0.1:8000/file/getMessages',{sender:sender,receiver:receiver})
  }

  
  
}
