import { Component, OnInit, ViewChild, Inject, ElementRef } from '@angular/core';
import { Validators, FormBuilder } from '@angular/forms';
import { DateListService } from 'src/app/configure/date-list.service';
import { PaymentDetailsService } from '../payment-details.service';
import { NotificationService } from 'src/app/registration/notification.service';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { DisburseData } from 'src/app/shared/generated_bill.model';
import { from } from 'rxjs';
import { MatDialog,MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { HttpClient } from '@angular/common/http';
import { DOCUMENT } from '@angular/common'; 
import * as XLSX from 'xlsx'; 
import { ModalService } from '../../_modal';

@Component({
  selector: 'app-weekly-disbursements',
  templateUrl: './weekly-disbursements.component.html',
  styleUrls: ['./weekly-disbursements.component.css']
})
export class WeeklyDisbursementsComponent implements OnInit {

  years=['2017-18','2018-19','2019-20','2020-21']
  weeknos=[1,2,3,4,5,6,7,8,9]
  status=["Notified","Partial","Complete"]
  paytype=["RTGS","DD","NEFT","Others"]
  payorrecieve=["Payable","Receivable"]
  startDate:Date;
  endDate:Date;
  listData: MatTableDataSource<DisburseData>;
  listData1: MatTableDataSource<DisburseData>;
  prevdueinpool:number;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort1: MatSort;
  @ViewChild(MatPaginator) paginator1: MatPaginator;
  displayedColumns: string[] = ['Week_No','Entity','Deviation_Final','Disbursement_Date','Payment_date','Payment_amount','Outstandings'];

  displayedColumns1: string[] = ['Week_No','Entity','BillDate','Deviation_Final'];

  showFooter='off';
  istableexists:boolean=false;
  constructor(private fb:FormBuilder,private date:DateListService,private disburse:PaymentDetailsService,private notification:NotificationService,public dialog: MatDialog) { }

  Disbursements=this.fb.group({
    'year':['2020-21',Validators.required],
    'weekno':[null,Validators.required],
    'weekStartDate':[null,Validators.required],
    'weekEndDate':[null,Validators.required],
  })
  
  ngOnInit(): void {
  }

  weekChange(event)
  {
    this.startDate=this.date.date_list[0][0];
    this.endDate=this.date.date_list[0][1];
  }

  onSubmit()
  {
    if(this.Disbursements.valid)
    {
      this.disburse.Disbursements(this.Disbursements.value).subscribe(res=>
        {
        

          this.listData = new MatTableDataSource(res[0]['disburse_pay']);
          this.listData.sort=this.sort;
          this.listData.paginator=this.paginator;
          this.showFooter='on';

          this.listData1 = new MatTableDataSource(res[1]['disburse_receive']);
          this.listData1.sort=this.sort1;
          this.listData1.paginator=this.paginator1;
          
          for(var val in res[2]['total_inpool'])
          {
            this.prevdueinpool=res[2]['total_inpool'][val]['dueinpool']
            
          }

          this.istableexists=true;
          
        });
    }
    else{
      this.notification.warn("Please Choose Required Fields");
    }
  }
  doDisbursement()
  {
    var to_pool=this.listData['filteredData'].reduce((accm,curr) =>accm + +curr.DevFinal - +curr.Outstandings,0);
    var from_pool=this.listData1['filteredData'].reduce((accum, curr) => accum + +curr.DevFinal,0);

    
    if(to_pool > from_pool)
    {
      
      this.openDialog();
      
    }
    else
    {
      alert("Do the pro rata basis yes/no")
    }
  }

  openDialog() {
    const dialogRef = this.dialog.open(DialogContentExampleDialog,{
     
      height:'500px',
      width:'70%',
      data: {disburse:this.Disbursements.value,paiddetails:this.listData['filteredData'],receivedetails:this.listData1['filteredData'],
      prevdueinpool:this.prevdueinpool}
    });
    
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }
    
  public calculateDevTotal() {
    return this.listData['filteredData'].reduce((accum, curr) => accum + +curr.DevFinal,0);
    
  }
  public calculateTotPaid() {
    return this.listData['filteredData'].reduce((accm,curr) => accm + +curr.DevFinal - +curr.Outstandings,0);
    
  }
  public calculateOutTotal() {
    return this.listData['filteredData'].reduce((accum, curr) => accum + +curr.Outstandings,0);
    
  }
  public calculateDevTotal1() {
    return this.listData1['filteredData'].reduce((accum, curr) => accum + +curr.DevFinal,0);
    
  }
}

@Component({
  selector: 'dialog-content-example-dialog',
  templateUrl: 'disburse-withoutprorata.html',
})
export class DialogContentExampleDialog implements OnInit {
  listData:any=[];
  totaldev:Number;
  totaldisbursed:number=0;
  @ViewChild('distable', { static: false }) distable: ElementRef;
  constructor(
    public dialogRef: MatDialogRef<DialogContentExampleDialog>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private http:HttpClient,
    private notification:NotificationService,
    public dialog:MatDialog) {}
  
  total_paidamount=this.data.paiddetails.reduce((accum, curr) => accum + +curr.DevFinal,0)
  total_tobereceived=this.data.receivedetails.reduce((accum, curr) => accum + +curr.DevFinal,0);
  fileName= 'ExcelSheet.xlsx'; 
  ngOnInit(){
    
  }
  public calculateDevTotal() {
      // return this.data.paiddetails.reduce((accum, curr) => accum + +curr.DevFinal,0);
      return this.total_paidamount;
    }
  public calculateAmountPaid() {
    return this.data.paiddetails.reduce((accm,curr) => accm + +curr.DevFinal - +curr.Outstandings,0);

  }
  public calculateOutTotal() {
    return this.data.paiddetails.reduce((accum, curr) => accum + +curr.Outstandings,0);
    
  }
  public calculateDevTotal1() {
    // return this.data.receivedetails.reduce((accum, curr) => accum + +curr.DevFinal,0);
    return this.total_tobereceived;
    
  }
  public calculateDisburseAmount(index:string) {
    var paid_amount=this.data.paiddetails.reduce((accm,curr) => accm + +curr.DevFinal - +curr.Outstandings,0);
    var disburse=this.data.receivedetails.reduce((accum, curr) => accum + +curr.DevFinal,0);
    if(paid_amount>disburse){
      
      return this.data.receivedetails[index]['DevFinal'];
      
    }
    else{
  
    return (this.data.receivedetails[index]['DevFinal']/this.data.receivedetails.reduce((accum, curr) => accum + +curr.DevFinal,0))*this.data.paiddetails.reduce((accm,curr) => +curr.DevFinal - +curr.Outstandings,0);
  
    }
  }
  public calculateDueToPool(index:string) {
    var paid_amount=this.data.paiddetails.reduce((accm,curr) =>accm + +curr.DevFinal - +curr.Outstandings,0);
    var disburse=this.data.receivedetails.reduce((accum, curr) => accum + +curr.DevFinal,0);
    if(paid_amount>disburse){
      
      return 0;
    }
    else{
      var entity_disburse=this.data.receivedetails[index]['DevFinal']/this.data.receivedetails.reduce((accum, curr) => accum + +curr.DevFinal,0)*this.data.paiddetails.reduce((accm,curr) => accm+ +curr.DevFinal - +curr.Outstandings,0)
      return this.data.receivedetails[index]['DevFinal']-entity_disburse;
    }

  }
  public calculateTotDisbursementAmount() {
    var paid_amount=this.data.paiddetails.reduce((accm,curr) =>accm + +curr.DevFinal - +curr.Outstandings,0);
    var disburse=this.data.receivedetails.reduce((accum, curr) => accum + +curr.DevFinal,0);

    if(paid_amount>disburse){
      
      return disburse;
      
    }
    else {
      for(const key in this.data.receivedetails)
      {
       var entity_amt= this.data.receivedetails[key]['DevFinal']
       var cumulative_amt=(entity_amt/disburse)*paid_amount
       this.totaldisbursed=this.totaldisbursed+cumulative_amt;
      }
      return this.totaldisbursed;
    }
  }
  getCurrentDate(){
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();

    return dd +'-'+mm+'-'+yyyy;
  }
  ProceedDisbursement() {
    this.http.post('http://127.0.0.1:8000/file/finaldisbursements',this.data).subscribe(res=>{
      this.notification.success("Successfully Done the Disbursement!");
      this.dialogRef.close();
    })
  }

  CreateExcel() {
    const ws: XLSX.WorkSheet =XLSX.utils.table_to_sheet(this.distable.nativeElement);
  
    const wb: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

    XLSX.writeFile(wb, this.fileName);
  }

  openConfirmationDialog()
  {
    const dialogRef1=this.dialog.open(ConfirmationDialog,{
      height:'150px',
      data:this.data
    });

    dialogRef1.afterClosed().subscribe(result => {
      if(result =='closed')
      {
        console.log("No action");
        
      }
      else{
        this.notification.success("Successfully Done the Disbursement!");
        this.dialogRef.close();
      }
      
    });
  }

  

}

@Component({
  template:`
  <h1 mat-dialog-title>Are you sure want to Proceed</h1>
  <div mat-dialog-actions>
    <button mat-raised-button color="primary" style="margin-left:5px;" (click)="ProceedDisbursement()">Yes</button>
    <button mat-raised-button color="warn" [mat-dialog-close]="'closed'" style="margin-left:5px;">Close</button>
  </div>
  `
})
export class ConfirmationDialog {

  constructor(private http:HttpClient,@Inject(MAT_DIALOG_DATA) public data: any,
  public dialogRef: MatDialogRef<ConfirmationDialog>){}
  ProceedDisbursement() {
    
    this.http.post('http://127.0.0.1:8000/file/finaldisbursements',this.data).subscribe(res=>{
      
      this.dialogRef.close();
    })
  }
}