import { Component, OnInit, ViewChild } from '@angular/core';
import { ContactEdit } from 'src/app/shared/form-data.model';
import { HttpClient } from '@angular/common/http';
import { ModalService } from 'src/app/_modal';
import { FormGroup, FormControl } from '@angular/forms';
import { NotificationService } from 'src/app/registration/notification.service';

@Component({
  selector: 'app-pending-contact',
  templateUrl: './pending-contact.component.html',
  styleUrls: ['./pending-contact.component.css']
})
export class PendingContactComponent implements OnInit {

  pendingContact=[];
  selected:any;
  anypendingDetails:boolean=true;
  constructor(private http:HttpClient,private modalService: ModalService,private notification:NotificationService) { }

  editPopup:FormGroup=new FormGroup({
    'fullname':new FormControl(null),
    'contactName':new FormControl(null),
    'designation':new FormControl(null),
    'telephone':new FormControl(null),
    'contactName2':new FormControl(null),
    'designation2':new FormControl(null),
    'telephone2':new FormControl(null),
    'contactName3':new FormControl(null),
    'designation3':new FormControl(null),
    'telephone3':new FormControl(null),

  })

  ngOnInit(): void {

    this.http.get('http://127.0.0.1:8000/editedpendingContacts').subscribe(response=>{
      
      for(const key in response){
          // this.anypendingDetails=true;
          this.pendingContact.push(response[key]);
      }
    },error=>{
      console.log("error occured",error);
    })
  }
  onView(contact){
    this.editPopup.patchValue(contact);
    this.selected=contact;
    
  }
  onApprove(){
    
    this.http.post('http://127.0.0.1:8000/final-edit-approval',{id:this.selected.id,register_id:this.selected.register_id_id}).subscribe(response=>{
      this.onFetch();
    this.modalService.close('edit-modal');
    },error=>{
      console.log(error);
      this.notification.warn("Failed to Update Details");
    });
  }

  onReject(){

    this.http.post('http://127.0.0.1:8000/final-edit-rejection',{id:this.selected.id,register_id:this.selected.register_id_id}).subscribe(response=>{
      this.onFetch();
      this.modalService.close('edit-modal');
    },error=>{
      this.notification.warn("Error Occured!");
    });

  }
  onFetch(){
    return this.http.get('http://127.0.0.1:8000/editedpendingContacts').subscribe(response=>{
          
          this.pendingContact=[];
          for(const key in response){
              
              this.pendingContact.push(response[key]);

          }
          console.log(this.pendingContact);
          
          this.notification.success("Approved Successfully")
          },error=>{
          
          this.notification.warn("Failed to Reject Contact Details");
        });
  }
  openModal(id: string) {
    this.modalService.open(id);
  }
  
  
openModal1(id: string) {
  
    this.modalService.open(id);
}

closeModal(id: string) {
    this.modalService.close(id);
}
}
