import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NotificationService } from 'src/app/registration/notification.service';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { ConfirmedValidator } from './userdetails.validator';

@Component({
  selector: 'app-userdetails',
  templateUrl: './userdetails.component.html',
  styleUrls: ['./userdetails.component.css']
})
export class UserdetailsComponent implements OnInit {

  usernamelist:Array<string>=[];
  selected:string;
  errormessage:string;
  matselected:boolean=false;
  constructor(private http:HttpClient,private notificationService:NotificationService,
              private fb:FormBuilder) { }

  changePassword=this.fb.group({
    'selecteduser':new FormControl(null,Validators.required),
    'password':new FormControl(null,[Validators.required]),
    'confirm_password':new FormControl(null,Validators.required)
  },{ 
    validator: ConfirmedValidator('password', 'confirm_password')
  })

  get f(){
    return this.changePassword.controls;
  }
   
  ngOnInit(): void {
    this.http.get('http://127.0.0.1:8000/getusers').subscribe(usernames=>{
      console.log("succesfully fetched users",usernames);
      
            for (const key in usernames) {
              this.usernamelist.push(usernames[key]);
                
            }
            return this.usernamelist;
        
    },error=>{
      this.errormessage=error;
      this.notificationService.warn("Error Occured,Please close the window and try again")
    })
  }
  choosen(event){
    if(event.value){
      this.matselected=true;
      console.log(this.matselected);
    }

  }
  selecteduser(){
  
    this.http.post('http://127.0.0.1:8000/logindetails',this.changePassword.value).subscribe(res=>{

      this.notificationService.success("Password Changed Successfully");
      this.changePassword.reset();
      // console.log("succesfully downloaded",res['content']);
      // var fileText=res['content'];
      // var fileName = this.selected+".txt"
      // // this.saveTextAsFile(fileText, fileName);
    
    },error=>{
      this.notificationService.warn("Error Occured,Try again later");
    })
  }
  saveTextAsFile(data,filename){

    if(!data) {
        console.error('Console.save: No data')
        return;
    }

    if(!filename) filename = 'console.json'

    var blob = new Blob([data], {type: 'text/plain'}),
        e    = document.createEvent('MouseEvents'),
        a    = document.createElement('a')
// FOR IE:

    if (window.navigator && window.navigator.msSaveOrOpenBlob)
      {
        window.navigator.msSaveOrOpenBlob(blob, filename);
      }
    else
      {
        var e = document.createEvent('MouseEvents'),
            a = document.createElement('a');

        a.download = filename;
        a.href = window.URL.createObjectURL(blob);
        a.dataset.downloadurl = ['text/plain', a.download, a.href].join(':');
        e.initEvent('click', true, false);
        a.dispatchEvent(e);
      }
    }



}
