import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { PostsService } from '../registration/registration.service';
import { Post } from '../shared/form-data.model';
import {MatDialog,MatDialogConfig, MatDialogRef} from '@angular/material/dialog';
import { RegistrationComponent } from '../registration/registration.component';
import { MatTableDataSource } from '@angular/material/table';
import {MatPaginatorModule, MatPaginator} from '@angular/material/paginator';
import {MatSortModule, MatSort} from '@angular/material/sort';
import { RegisterPopupComponent } from '../registration/register-popup/register-popup.component';
import { Subject, throwError } from 'rxjs';
import { NotificationService } from '../registration/notification.service';
import { ModalService } from '../_modal';
import { BigModalService } from '../_bigmodal';
import { HttpClient } from '@angular/common/http';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { FileLikeObject, FileUploader } from 'ng2-file-upload';
@Component({
  selector: 'app-super-user',
  templateUrl: './super-user.component.html',
  styleUrls: ['./super-user.component.css']
 
})
export class SuperUserComponent implements OnInit {
  
  allowHeader=true;
  loadedPosts:Post[]=[];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  listData: MatTableDataSource<Post>;
  error=new Subject<string>();
  shortName:string;
  searchKey:string;
  imageURLList:Array<string>[]=[];
  emptyList:boolean=false;
  selectedRow:string;
  isFinance:boolean=false;
  financeRemarks:string;
  status:boolean=false;
  status1:boolean=false;
  isreadOnly:boolean=false;
  currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
  displayedColumns: string[] = ['entityName','userCategory','applicationNo','accountNo','branchName','actions'];
  public uploader: FileUploader = new FileUploader({});
  public hasBaseDropZoneOver: boolean = false;
  constructor(private posts:PostsService,public dialog:MatDialog,
        private crudData:PostsService,
        private notificationService:NotificationService,
        private modalService: ModalService,
        private modalService1: BigModalService,
        public popupUser:PostsService,
        private http:HttpClient) { 
 
  }
 
  approvaldocs:FormGroup=new FormGroup({
    'pannumber':new FormControl(null,Validators.required),
    'gstinnumber':new FormControl(null,Validators.required)
  })

  ngOnInit(): void {
    this.popupUser;

    
    this.posts.onFetchPosts().subscribe(
      posts=>{
            console.log('After Mapping',posts);
            this.listData = new MatTableDataSource(posts);
            
            this.listData.sort=this.sort;
            this.listData.paginator=this.paginator;
            this.loadedPosts=posts;
       
      }
    );

    if(this.currentUser.usertype == 'Finance'){
      this.isFinance=true;
      this.isreadOnly=false;
    }
    else{
      this.isreadOnly=true;
    }
  }
// Filter Contents 
  onSearchClear() {
    this.searchKey = "";
    this.applyFilter();
  }

  applyFilter() {
    this.listData.filter = this.searchKey.trim().toLowerCase();
  }
  onSubmit(){
    this.allowHeader=false;
    
  }
  
  
  onOpen(){
    
    // console.log(this.loadedPosts[+indexEl]);
    // this.dialog.open(RegistrationComponent)
  }

  onEdit(row){
    
    this.crudData.populateForm(row);
    this.selectedRow=row.id;
    this.http.post('http://127.0.0.1:8000/getimageurls',row.register_id).subscribe(res=>{
    
      this.imageURLList=[];
      for(const key in res){
         
        this.imageURLList.push(res[key]);
          }
        });

        if(this.imageURLList.length<1){
          this.emptyList=true;
        }
        console.log("response",this.imageURLList);
  
  }

  checkDetails(status:string){

    this.http.post('http://127.0.0.1:8000/financeVerification',{rowid:this.selectedRow,status:status,remarks:this.popupUser.popupForm.value['remarks']}).subscribe(res=>{
      
      this.modalService.close('edit-modal');
      if(status =='approve'){
        this.notificationService.success("Approved Successfully");
      }
      else if(status =='reject'){
        this.notificationService.warn("Rejected Successfully");
      }
      
    },error=>{
      
      this.notificationService.warn("Error Occured");
    })
  }

  
  fileOverBase(event):  void {
    this.hasBaseDropZoneOver  =  event;
  }
  getFiles(): FileLikeObject[] {
    return this.uploader.queue.map((fileItem) => {
      return fileItem.file;
    });
  }
  upload(id:string){

    let files = this.getFiles();
    let requests = [];
    // files.forEach((file) => {
      let extension=files[0].name
      let formData = new FormData();
      formData.append('file' , files[0].rawFile, this.selectedRow+'@'+id+extension);
         
      return this.http.post('http://127.0.0.1:8000/imageData',formData).subscribe(res=>{
        this.status=true;
      },error=>{
        this.notificationService.warn("Not Uploaded")
      }) 
  }

  upload1(id:string){

    let files = this.getFiles();
    let requests = [];
    // files.forEach((file) => {
      let extension=files[0].name
      let formData = new FormData();
      formData.append('file' , files[0].rawFile, this.selectedRow+'@'+id+extension);
         
      return this.http.post('http://127.0.0.1:8000/imageData',formData).subscribe(res=>{
        this.status1=true;
      },error=>{
        this.notificationService.warn("Not Uploaded")
      }) 
  }
  onApprove(){
    
    this.crudData.approveForm(this.selectedRow,this.popupUser.popupForm.value['adminremarks']).subscribe(
      responseData =>{
        console.log("Approved Response",responseData);
        this.posts.onFetchPosts().subscribe(
          posts=>{
            this.listData = new MatTableDataSource(posts);
            this.listData.sort=this.sort;
            this.listData.paginator=this.paginator;
            this.loadedPosts=posts;
            this.notificationService.success('! Approved successfully');
          }
        );
        
      }
    );
    
    
  }
  
  onDelete(){
    
   
   this.crudData.deleteUser(this.selectedRow,this.popupUser.popupForm.value['adminremarks']).subscribe(
    responseData =>{
      this.posts.onFetchPosts().subscribe(
        posts=>{
          this.listData = new MatTableDataSource(posts);
          this.listData.sort=this.sort;
          this.listData.paginator=this.paginator;
          this.loadedPosts=posts;
          this.notificationService.warn('! Rejected successfully');
        }
      );
      
    },error=>{
        this.error.next(error.message);
    }
  );
  
  }
  
  openModal(id: string) {
    this.modalService.open(id);
  }
  f1(url:string){

    const link = document.createElement('a');
    link.setAttribute('target', '_blank');
    link.setAttribute('attr.href', url);
   
    link.setAttribute('download', `cheque.jpeg`);
    document.body.appendChild(link);
    
    link.click();
    console.log(link);
    link.remove();
   
  }
  
openModal1(id: string) {
    this.modalService1.open(id);
}

closeModal(id: string) {
    this.modalService.close(id);
}

closeModal1(id: string) {
    this.modalService1.close(id);
}
  
}
