import { Component, ElementRef, OnChanges, OnInit, ViewChild } from '@angular/core';
import { ChatServiceService } from '../services/chat-service.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-new-chat',
  templateUrl: './new-chat.component.html',
  styleUrls: ['./new-chat.component.css']
})
export class NewChatComponent implements OnInit,OnChanges {

  users:Array<string>=[];
  feed:any=[];
  receiver:string;
  sender:string;
  message:string;
  currentUser = JSON.parse(sessionStorage.getItem('currentUser'));
  @ViewChild('scroller') private myScroller: ElementRef;

  constructor(private chat:ChatServiceService,private http:HttpClient) { }

  ngOnInit(): void {
    this.chat.getUsers().subscribe(users => {
      for(const key in users)
      {
        this.users.push(users[key]);
      }
  });
  }

  ngAfterViewChecked() {
    this.scrollToBottom();
  }

  scrollToBottom(){
    this.myScroller.nativeElement.scrollTop
    = this.myScroller.nativeElement.scrollHeight;
  }
  onSelectUser(receiver:string){
    this.feed=[];
    this.sender=this.currentUser['userName'];
    this.receiver=receiver[0];
    this.http.post('http://127.0.0.1:8000/file/getMessages',{sender:this.currentUser['userName'],receiver:receiver[0]}).subscribe(res=>{
        for (const key in res) 
        {
          this.feed.push(res[key]);
        }
    });   
  }

  onSendMessage(){

    this.http.post('http://127.0.0.1:8000/file/chatMessages',{message:this.message,
      sender:this.currentUser['userName'],receiver:this.receiver,short_name: this.currentUser.shortName}).subscribe(res=>{
        this.ngOnChanges(res);
        this.message='';
      })
  }

  handleSubmit(event) {
    if (event.keyCode === 13) {
      this.onSendMessage();
    }
  }

  onCheckSenderSide(message:any){
    if(message['sender']==this.sender && message['receiver']==this.receiver)
    {
      return true;
    }
    else{
      return false;
    }
  }

  onCheckReceiverSide(message:any){
    if(message['sender']==this.receiver && message['receiver']==this.sender)
    {
      return true;
    }
    else{
      return false;
    }
  }
  ngOnChanges(allmsg:any)
  { 
    this.feed=[];
      for (const key in allmsg) 
        {
          this.feed.push(allmsg[key]);
        }
      this.myScroller.nativeElement.scrollTop = this.myScroller.nativeElement.scrollHeight;
  }
}
