import { Component, OnInit, ViewChild } from '@angular/core';
import { Validators, FormBuilder } from '@angular/forms';
import { PaymentDetailsService } from '../payment-details.service';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { ViewPaymentsModel } from '../../shared/generated_bill.model';
import { SelectionModel } from '@angular/cdk/collections';
import { ModalService } from 'src/app/_modal';
import { NotificationService } from 'src/app/registration/notification.service';
import { BigModalService } from 'src/app/_bigmodal';
import { DateListService } from 'src/app/configure/date-list.service';


@Component({
  selector: 'app-pending-payments',
  templateUrl: './pending-payments.component.html',
  styleUrls: ['./pending-payments.component.css']
})
export class PendingPaymentsComponent implements OnInit {

  years=['2017-18','2018-19','2019-20','2020-21']
  weeknos=[1,2,3,4,5,6,7,8,9]
  status=["Created","Approved","Rejected"]
  
  listData: MatTableDataSource<ViewPaymentsModel>;
  selection = new SelectionModel<ViewPaymentsModel>(false);
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  error=null;
  displayedColumns=["select","Pay Type","Pay refno","Paid_by","Paid_for","DevFinal","Pay_date","Pay_amount","actions"]
  payorrecieve=["Payable","Receivable"]
  selectedCheckbox:ViewPaymentsModel;
  paymentButton:Boolean=true;
  previousPayments=[];
  remarks:string;
  startDate:Date;
  endDate:Date;
  index:any;
  constructor(private fb:FormBuilder,private vs:PaymentDetailsService,public checked1:PaymentDetailsService,private modalService: ModalService,
    private notificationService:NotificationService,private modalService1:BigModalService,private date:DateListService) { }

  viewpayments=this.fb.group({
    'year':['2020-21',Validators.required],
    'weekno':[null,Validators.required],
    'weekStartDate':[null,Validators.required],
    'weekEndDate':[null,Validators.required],
    'payorreceive':["Payable",Validators.required],
    'status':["Created",Validators.required]
  })
  

  ngOnInit(): void {
  }

  onViewPayments(){
    this.vs.viewPayments(this.viewpayments.value).subscribe(viewres=>{
      this.listData = new MatTableDataSource(viewres[0]['viewpayments']);
     
      this.listData.sort=this.sort;
      this.listData.paginator=this.paginator;
  
    });
  }
  onSelect(element){
    this.selectedCheckbox=element;
    console.log(this.selectedCheckbox);
  }
  visibility(event){
    console.log("checking happened",event['checked']);
    this.paymentButton=!event['checked'];
  }

  weekChange(event)
  {
    this.startDate=this.date.date_list[0][0];
    this.endDate=this.date.date_list[0][1];
  }
  onView(element,index){
      this.index=index;
      this.checked1.populateViewForm(element);
      this.previousPayments=[];
      this.vs.previousPayments(element).subscribe(res=>
        {
          for(var key in res)
          {
            this.previousPayments.push(res[key]);
          }
        })

    }

    openModal(id:string) {
    this.modalService.open(id);
    }

    openModal1(id:string) {
    this.modalService1.open(id);
    }
    
    
    closeModal(id:string) {
    this.modalService.close(id);
    }

    closeModal1(id:string) {
    this.modalService1.close(id);
    }

    onSaveChanges()
    {
      console.log(this.checked1.selectedViewPayment.value);
      
        this.listData.filteredData[this.index]['Pay_date']=this.checked1.selectedViewPayment.value['Pay_date']
      
      
      // this.checked1.selectedViewPayment.patchValue({Pay_date:this.checked1.selectedViewPayment.value['Pay_date']})
      this.modalService.close('view-details');
    }

    onApprove(){
      // 
      if(this.selectedCheckbox.id)
      { 
      this.checked1.approvePayment(this.selectedCheckbox.id,this.listData.filteredData[this.index]['Pay_date']).subscribe(res=>{
        this.notificationService.success('! Approved successfully');
        this.onViewPayments();
        this.closeModal1('approve-modal');
      },error=>{
        this.notificationService.warn("First Please View the Payment Details");
        this.error.next(error.message);
    })
    }
    else{
      this.notificationService.warn("Not Allowed to Save!")
    }
    }
    onReject(){
      if(this.selectedCheckbox.id)
      {
      this.checked1.rejectPayment(this.selectedCheckbox.id,this.remarks).subscribe(res=>{
        this.notificationService.warn('! Rejected successfully');
        this.onViewPayments();
        this.closeModal1('reject-modal');
      },error=>{
        this.error.next(error.message);
    })
    }
    else{
      this.notificationService.warn("You are not allowed !!")
    }
  } 
  
}
