  ngOnInit() {
  
    var currentUser = JSON.parse(sessionStorage.getItem('currentUser'));

    console.log("current user",currentUser);
    this.isAuthenticated=currentUser.isSuperUser;
    this.fullname=currentUser.fullname;
    this.usercategory=currentUser.usercategory;
    

    this.http.post('http://127.0.0.1:8000/rejectlist',currentUser.registration_id).subscribe(response=>{

      for(const key in response)
        { 
         
          if(key=="user_formdata")
          { 
            
            this.postService.signupForm.patchValue(response[key])
            this.fetchedResults.push(response[key]);
          }
        }
        for(const key in response)
        {
          if(key =="readonly")
          {
            
            if(this.isAuthenticated){
              this.readOnlyStatus=false
            }
            else if(response[key]){
              
              this.readOnlyStatus=true;
            }
            else{
              this.readOnlyStatus=false
            }
          }
        } 
        // for(const key in response){
        //   console.log("for loop data",response[key]['entityName']);
        // }
    },error=>{
      console.log("Error occured");
    })
    //To Handle the errors explicitly ,refer ErrorHandling Angular Course
    this.errorSub=this.postService.error.subscribe(errorMessage =>{
      this.error=errorMessage;
    })
    this.fetchPosts();
    }

