upload(banktype:string){
  let files = this.getFiles();
  let requests = [];
  
  if(files[0]){
    let extension=files[0].name
    let formData = new FormData();
    formData.append('file' , files[0].rawFile, this.currentUser.userName+'@'+banktype+extension);     
    return this.http.post('http://127.0.0.1:8000/imageData',formData)  
  }
  else{
    this.notification.warn("Please Choose File");
  }
  }

