openModal(id: string) {
  
  this.popupUser.popupForm.patchValue(this.popupUser.signupForm.value);
  console.log(this.popupUser.signupForm.value)
  this.modalService.open(id);
}

openFormDialog(){

  const dialogRef = this.dialog.open(FormDialog, {
    width: '750px',
    autoFocus: false,
    maxHeight: '90vh',
    data: {formdata:this.popupUser.signupForm.value}
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    
  });
}


closeModal(id: string) {
  this.modalService.close(id);
}

