@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'form-data.html',
})
export class FormDialog implements OnInit{

  error=null;
  constructor(
    public dialogRef: MatDialogRef<FormDialog>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public popupUser:PostsService,
    public jwmodalpopup:PopupDataService,
    public service:PostsService,
    private postService:PostsService,
    private router:Router,) {}

  ngOnInit(){
    console.log(this.data);
    
  }
  onSubmit(){
    let postData:Post =this.service.signupForm.value;
    console.log(postData);
    
    var currentUser = JSON.parse(sessionStorage.getItem('currentUser'));

      this.postService.onSendPosts(postData,currentUser.registration_id).subscribe(
        responseData =>{
          this.dialogRef.close();
          this.router.navigate(['/registration/submitted']);
        },error=>{
            this.error.next(error.message);
        }
      );
  }
  onNoClick() {
    this.dialogRef.close();
  }

}             
