import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { UsercreationService } from './usercreation.service';
import { NotificationService } from '../registration/notification.service';
import { FileUploader, FileLikeObject } from 'ng2-file-upload';

@Component({
  selector: 'app-usercreation',
  templateUrl: './usercreation.component.html',
  styleUrls: ['./usercreation.component.css']
})
export class UsercreationComponent implements OnInit {

  status1:boolean=false;
  userCategory:Array<string>=['Generating Station','Seller','Buyer','Transmission Licensee',
                              'Distribution Licensee','Trading Licensee','Power Exchange',
                            'Battery Energy Storage system','QCA / Aggregators','Others'];

  userPrivileges:Array<string>=['General Member','Finance']
  constructor(private fb:FormBuilder,private http:HttpClient,
    private newuser:UsercreationService,
    private notificationService:NotificationService) { }
  
  public uploader: FileUploader = new FileUploader({});
  public hasBaseDropZoneOver: boolean = false;
  ngOnInit(): void {
  }
  userCreation=this.fb.group({
    'fullname':[null,Validators.required],
    'dateofregistration':[null,Validators.required],
    'shortname':[null,Validators.required],
    'userCategory':[null,Validators.required],
    'userPrivileges':['General Member',Validators.required],
    'mobileno':[null,Validators.required],
    'entityname':[null,Validators.required],
    'fncid':[null,Validators.required],
    'email':["test@test.com",Validators.required],
    'alias1':[null,Validators.required],
    'alias2':[null,Validators.required],
    'alias3':[null,Validators.required],
    'alias4':[null],
  })

  fileOverBase(event):  void {
    this.hasBaseDropZoneOver  =  event;
  }
  getFiles(): FileLikeObject[] {
    return this.uploader.queue.map((fileItem) => {
      return fileItem.file;
    });
  }
  upload() {   
    
    // var token = currentUser.token; // your token
  // console.log(currentUser.shortName);
  let files = this.getFiles();
  let requests = [];
  console.log("selected files",files[0]);
  // files.forEach((file) => {
    let formData = new FormData();
    let extension=files[0].name
    console.log("extension file",this.userCreation.value['fncid']+extension);
    formData.append('file',files[0].rawFile,this.userCreation.value['fncid']+'@'+extension);
    
    if(!this.userCreation.value['shortname']){
      this.notificationService.warn("Please enter Registration id of entity")
    }
    else{
      this.http.post('http://127.0.0.1:8000/registerformimage',formData).subscribe(
      res=>{
        console.log("respond from image view")
        this.status1=true;
        this.uploader.clearQueue();
      },
      err => {  
        console.log(err);
      }
    ); 
    }
        
  
}
  onSubmit(){
  
    this.newuser.createUser(this.userCreation.value).subscribe(res=>{
      this.notificationService.success('! User Created Succefully');
      this.userCreation.reset();
    },error=>{
      console.log("error occured",error.message);
      this.userCreation.reset();
      this.notificationService.warn('! Error has occured while creating user');
      this.notificationService.warn('! Please check whether user already exists or not');
      
    })
   }
  
}


