import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { PostsService } from 'src/app/registration/registration.service';
import { ModalService } from 'src/app/_modal';
import { PopupDataService } from 'src/app/shared/popup-data.service';

@Component({
  selector: 'app-all-users',
  templateUrl: './all-users.component.html',
  styleUrls: ['./all-users.component.css']
})
export class AllUsersComponent implements OnInit {

  allUsersList=[];
  imageURLList:Array<string>[]=[];
  emptyList:boolean=true;
  constructor(private http:HttpClient,public popupUser:PostsService,private modalService:ModalService,
    public jwmodalpopup:PopupDataService) { }

  ngOnInit() {

    this.http.get('http://127.0.0.1:8000/get-all-users').subscribe(res=>
    {
      
      for(const key in res)
      {
        
        this.allUsersList.push(res[key]);
      }
    },
    error=>{
      console.log("error occured",error);
    })
  }

  openModal(id: string,ref:string) {
    
    this.popupUser.popupForm.patchValue(this.allUsersList[ref]);
    this.modalService.open(id);
  }

  openModal1(id: string,ref:string) {
    this.http.post('http://127.0.0.1:8000/getimageurls',this.allUsersList[ref]['register_id']).subscribe(res=>{
    
        this.imageURLList=[];
          for(const key in res)
          {
            this.imageURLList.push(res[key]);

          }
          if(this.imageURLList.length > 1)
          {
            this.emptyList=false;
          }
          console.log(this.imageURLList.length);
        });

       
        
    this.modalService.open(id);
    
  }
  
  
  closeModal(id: string) {
    this.modalService.close(id);
  }
  
}
