import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { FormGroup, FormControl } from '@angular/forms';
import { BillData, ViewPaymentsModel } from '../shared/generated_bill.model';

@Injectable({
  providedIn: 'root'
})


export class PaymentDetailsService {

  currentUser = JSON.parse(sessionStorage.getItem('currentUser'));

  constructor(private http:HttpClient) { }

  selectedRow:FormGroup=new FormGroup({
    'id':new FormControl(null),
    'Letter_refno':new FormControl(null),
    'Revision_no':new FormControl(null),
    'DevFinal':new FormControl(null),
    'Outstandings':new FormControl(null),
    'PayableorReceivable':new FormControl(null),
    'remarks':new FormControl(null)

  })

  selectedViewPayment:FormGroup=new FormGroup({
    'id':new FormControl(null),
    'Pay_type':new FormControl(null),
    'Pay_refno':new FormControl(null),
    'Pay_date':new FormControl(null),
    'Pay_amount':new FormControl(null),
    'Srldc_payamount':new FormControl(null),
    'Remarks':new FormControl(null),
    'reject_remarks':new FormControl(null)

  })

  fetchConfigDetails(payment:BillData1){
   
    return this.http.post('http://127.0.0.1:8000/file/fetchConfigured',{payment:payment,username:this.currentUser.userName}) 
  }

  populateForm(row:BillData){
    
    this.selectedRow.patchValue(row);
    
  }

  populateViewForm(row:ViewPaymentsModel){
    
    this.selectedViewPayment.patchValue(row);
    
    // console.log("selected check box",this.selectedViewPayment);
    
  }
  previousPayments(element)
  {
    return this.http.post('http://127.0.0.1:8000/file/previousPayments',element)
  }
  paymentDetails(paymenttype:string,paymentref:string,paymentdate:string,paymentamount:Number,remarks:string,status:String,username:string){

    return this.http.post('http://127.0.0.1:8000/file/paymentDetails',{paymenttype,paymentref,paymentdate,paymentamount,remarks,status,username,bottomrow:this.selectedRow.value}) 
  }
  viewPayments(viewData:FormData){
    
    return this.http.post('http://127.0.0.1:8000/file/viewpayments',{data:viewData,username:this.currentUser.userName})
  }

  splitPayments(billdata)
  {
    return this.http.post('http://127.0.0.1:8000/file/splitpayments',billdata);
  }
  approvePayment(id,paydate){
    return this.http.post('http://127.0.0.1:8000/file/approvepayments',{id:id,date:paydate});
  }

  rejectPayment(id,remarks){
    return this.http.post('http://127.0.0.1:8000/file/rejectpayments',{id:id,remarks:remarks});
  }

  Disbursements(disburseWeek:FormGroup)
  {
    return this.http.post('http://127.0.0.1:8000/file/disbursements',disburseWeek);
  }
}

export interface BillData1{
  yearno:string,
  weekno:string
}